import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Map from '../../components/Map/Map';


class MapContainer extends Component {
  constructor() {
    super();
    this.state = { loading: true, error: null };
  }

  componentDidMount = () =>this.initDataMap()


  /**
   * Fetch Data
   */

  initDataMap = async () => {
    const { initDataMap,citySelected } = this.props;

    this.setState({ loadingMap: true, errorMap: null });

    try {
      const successMessage = await initDataMap(citySelected);
      this.setState({ loadingMap: false, errorMap: null ,successMap :successMessage});
      setTimeout(() => {this.setState({ successMap :null})}, 300);
    } catch (err) {
      this.setState({ loadingMap: false, errorMap: err.message });
    }
  }

  showFarmer = (farmer) => {
    /*const {saveFarmer} = this.props;
    try{
      saveFarmer(farmer)

    }catch(error){

    }*/
  }

  selectCity= (city) => {
    const { selectCity } = this.props;
    try {
      selectCity(city)
    } catch (err) {
      this.setState({ loadingSearch: false, errorSearch: err.message });
    }

  }

  onResearch= async (research) => {
    const { searchCity,updateMapFilter } = this.props;

    this.setState({ loadingSearch: true, errorSearch: null });

    try {
      updateMapFilter(research.productTypes)
      const listVilles = await searchCity(research.ville);
      if(listVilles && listVilles.length == 1 ){
        this.selectCityAndInitDataMap(listVilles[0])
      }else if (listVilles && listVilles.length > 1){
        this.setState({ listVilleResultSearch : listVilles })
      }


    } catch (err) {
      this.setState({ loadingSearch: false, errorSearch: err.message });
    }
  }

  selectCityAndInitDataMap=async (city)=>{
    try{
      this.setState({listVilleResultSearch:null, loadingSearch: false, errorSearch: null ,successSearch :"Votre ville a bien été trouvée."})
      await this.selectCity(city)
      setTimeout(() => {this.setState({ successSearch :null})}, 1500)
      this.initDataMap()
    }catch(err){
      this.setState({ loadingSearch: false, errorSearch: err.message });
    }
  }

  showUser = async(user)=>{
    user.startDate = new Date()
    user.endDate= new Date()
    this.setState({userSelected:user})
  }

  makeCommand = async(user)=>{
    const { makeCommand } = this.props;
    this.setState({ errorCommand: null, successCommand: null, loadingCommand: true });
    try {
      const success = await makeCommand(user);
      this.setState({ errorCommand: null, successCommand: success, loadingCommand: false});
      setTimeout(() => {this.setState({  successCommand: null,userSelected:null});}, 2000);
    } catch (error) {
      this.setState({ errorCommand: error.message, successCommand: null, loadingCommand: false  });
    }
  }
  /**
   * Render
   */
  render = () => {
    const { loadingSearch, errorSearch,successSearch, loadingMap, errorMap,successMap,listVilleResultSearch,userSelected,errorCommand, successCommand, loadingCommand } = this.state;
    const {user,lieux,listCategories,citySelected,mapFilter} = this.props;
    return <Map
     loadingSearch={loadingSearch}
     errorSearch={errorSearch}
     successSearch={successSearch}
     clickSearch={this.onResearch}
     loadingMap={loadingMap}
     errorMap={errorMap}
     successMap={successMap}
     user={user}
     lieux={lieux}
     reFetch={this.initDataMap}
     showFarmer={this.showFarmer}
     selectCityAndInitDataMap={this.selectCityAndInitDataMap}
     defaultValuesSearch={{ville : citySelected,productTypes:(!!mapFilter && mapFilter.length>1)?mapFilter:listCategories.reduce(function(map, obj) {map[obj] = true;return map;}, {})}}
     listVilleResultSearch={listVilleResultSearch}
     userSelected={userSelected}
     showUser={this.showUser}
     makeCommand={this.makeCommand}
     errorCommand={errorCommand}
     successCommand={successCommand}
     loadingCommand={loadingCommand}
     />;
  };
}

MapContainer.propTypes = {
  initDataMap: PropTypes.func.isRequired,
};

MapContainer.defaultProps = {
};

const mapStateToProps = (state) => ({
  user : state.user,
  citySelected : state.lieux.citySelected,
  lieux : state.lieux.listFilter,
  listCategories : state.products.listStatus,
  mapFilter : state.lieux.mapFilter
});

const mapDispatchToProps = (dispatch) => ({
  initDataMap: dispatch.lieux.initDataMap,
  saveFarmer : dispatch.lieux.saveFarmer,
  searchCity : dispatch.lieux.fetchCities,
  updateMapFilter : dispatch.lieux.updateMapFilter,
  selectCity : dispatch.lieux.replaceSelectCity,
  makeCommand: dispatch.commands.saveAll,
});

export default connect(mapStateToProps, mapDispatchToProps)(MapContainer);
