import moment from 'moment';
import axios from '../lib/api';
import HandleErrorMessage from '../lib/format-error-messages';
import initialState from '../store/lieux';
import Config from '../constants/config';
import { getFeaturedImageUrl } from '../lib/images';
import { errorMessages, successMessages } from '../constants/messages';
import config from '../constants/config';
import { connect } from 'react-redux';
import {getLatLngFromAdresse} from './../lib/localization';

export default {
  namespace: 'lieux',

  /**
   *  Initial state
   */
  state: initialState,

  /**
   * Effects/Actions
   */
  effects: (dispatch) => ({

    async initDataMap(lieu){
      try {
        const response = await axios.get("/lieux",{headers:{code:lieu.code}});
        const { data } = response;
        if (!data) {
          throw new Error(  errorMessages.lieuxFetchError );
        }
        this.replaceList(data.lieux)
        this.replaceListFiter(data.lieux)
        dispatch.user.replaceChoixCentre(data.centre)
        dispatch.products.replaceListStatus(data.productTypeList)
        return "lieux proches trouvés";
      } catch (error) {
        throw HandleErrorMessage(error);
      }
    },

    /**
     * Save date to redux store
     * @param {obj} data
     * @returns {Promise[obj]}
     */
    async save(lieu) {
      try {
        let position = await getLatLngFromAdresse(lieu.adress,lieu.city)
        lieu.lat=position.lat
        lieu.lng=position.lng
        lieu.categories=Object.keys(lieu.listStatus).filter(key => {if(lieu.listStatus[key]) return key;});
        const response = await axios.post("/lieu",lieu);
        const { data } = response;
        if (!data) {
          throw new Error(  errorMessages.usersEmpty );
        }
        //dispatch.products.addProductToMyProducts(data)
        return successMessages.defaultForm; // Message for the UI
      } catch (error) {
        throw HandleErrorMessage(error);
      }
    },

    async delete(id) {
      try {
        const response = await axios.get("/lieu/delete",{headers:{"idLieu":id}});
        const { status } = response;
        if (status!=200) {
          throw new Error(  "Problème lors de la suppression" );
        }
        dispatch.lieux.deleteReduxLieux(id)
        return successMessages.defaultForm; // Message for the UI
      } catch (error) {
        throw HandleErrorMessage(error);
      }
    },

    async validEmail(email) {
      try {
        const response = await axios.get("/lieu/valid",{headers:{"email":email}});
        const { status } = response;
        if (status!=200) {
          throw new Error(  "Problème lors de la validation" );
        }
        return successMessages.defaultForm; // Message for the UI
      } catch (error) {
        throw HandleErrorMessage(error);
      }
    },

  

    async fetchCities(city) {
      try {
        const response = await axios.get("/communes/find",{headers: {'name': city }})
        const { data } = response
        if (!data) {
          throw new Error(  errorMessages.errorFindCity );
        }
        return data;
      } catch (error) {
        throw HandleErrorMessage(error);
      }
    },

    saveFarmer(farmer){
      try{
        this.replaceCurrent(farmer)
        return true;
      }catch(error){
        throw HandleErrorMessage(error)
      }

    }
  }),

  /**
   * Reducers
   */
  reducers: {

    replaceCurrent(state, payload) {
      state.current = payload
      return {...state};
    },

    replaceList(state, payload) {
      state.list = payload
      return {...state};
    },
    replaceListFiter(state, payload) {
      state.listFilter = payload.filter((lieu) => {
        for(let categorie in state.mapFilter){
          if(!!state.mapFilter[categorie] &&  lieu.categories.indexOf(categorie) >-1) return lieu
        }
      })
      return {...state};
    },
    updateMapFilter(state, payload) {
      state.mapFilter = payload
      return {...state};
    },
    replaceSelectCity(state,payload){
      state.citySelected = payload
      return {...state}
    },
    replaceMyLieux(state,payload){
      state.me = payload
      return {...state}
    },
    deleteReduxLieux(state,payload){
      state.me = [...state.me].filter(product => product.id != payload)
      return {...state}
    }
  },
};
