import React  ,{ useEffect,useState } from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faMailchimp} from '@fortawesome/free-brands-svg-icons'

import {
  Container,
  Row,
  Col,

  Card,CardTitle,CardSubtitle,CardText,CardHeader,CardFooter,
  CardBody,
  Alert,
  Input,
  Label,Form,FormGroup,Button,Modal,ModalHeader,ModalBody,ModalFooter,ListGroup,ListGroupItem
} from 'reactstrap';
import { useForm } from 'react-hook-form';
import Template from '../Templates/Dashboard';
import {getCategoriesImage} from 'src/lib/images'

const LieuxComponent = ({
  errorAddLieu, loadingAddLieu, successAddLieu, clickAddLieu, defaultValues,errorDelete, successDelete,  loadingDelete,
   deleteLieu,myLieux,successAddLieuExistant,errorAddLieuExistant,emailLieuExistant,validEmail
}) => {
  const { register, handleSubmit, errors , setValue} = useForm({defaultValues});

  useEffect(() => {
    register({ name: 'name' }, { required: "Nom requis." });
    register({ name: 'adress' }, { required: "adresse requise"  });
    register({ name: 'city' }, { required: "ville requise"  });
    register({ name: 'horaireOuverture' }, { required: "horaire requis"  });
    register({ name: 'listStatus' })
  }, [register]);



  const [checkedItems, setCheckedItems] = useState(defaultValues.listStatus);
    const handleChangeCheckBox = event => {
      defaultValues.listStatus[event.target.name]= event.target.checked
      setCheckedItems({
        ...checkedItems,
        [event.target.name]: event.target.checked
      });
    };

  const updateListStatus=(data)=>{
    data.listStatus = defaultValues.listStatus
    clickAddLieu(data)
  }
  return (
    <Template pageTitle="Produit">
      <React.Fragment>
        <Container fluid={true}>
          <Row  sm="1" md="2">
            <Col  md="3">
              <Form onSubmit={handleSubmit(updateListStatus)}>
                    {successAddLieu && <Alert color="success">{successAddLieu}</Alert>}
                    {errorAddLieu && <Alert color="danger">{errorAddLieu}</Alert>}
                    <h1> Ajouter un lieu</h1>
                    <FormGroup>
                      <Label >Nom</Label>
                      {errors.name && <Alert color="danger">{errors.name.message}</Alert>}
                      <Input key="name" type="text" placeholder="name" name="name" defaultValue={defaultValues.name }
                      onChange={e => setValue("name",e.target.value)} invalid={!!errors.name} />
                    </FormGroup>


                    <FormGroup>
                      <Label >Adresse</Label>
                      {errors.adress && <Alert color="danger">{errors.adress.message}</Alert>}
                      <Input key="adress" type="text" placeholder="adresse" name="adress" defaultValue={defaultValues.adress }
                      onChange={e => setValue("adress",e.target.value)} invalid={!!errors.adress} />
                    </FormGroup>

                    <FormGroup>
                      <Label >Ville</Label>
                      {errors.city && <Alert color="danger">{errors.city.message}</Alert>}
                      <Input key="city" type="text" placeholder="ville" name="city" defaultValue={defaultValues.city}
                      onChange={e => setValue("city",e.target.value)} invalid={!!errors.city} />
                    </FormGroup>

                    <FormGroup>
                      <Label >Horaire</Label>
                      {errors.horaireOuverture && <Alert color="danger">{errors.horaireOuverture.message}</Alert>}
                      <Input key="horaireOuverture" type="text" placeholder="ville" name="horaireOuverture" defaultValue={defaultValues.horaireOuverture}
                      onChange={e => setValue("horaireOuverture",e.target.value)} invalid={!!errors.horaireOuverture} />
                    </FormGroup>


                    <FormGroup>
                      <Row >
                        <Col xs={1} />
                        <Col>
                          <Label >Type de produit vendus</Label>
                          <Row xs="2" sm="4" md="2" lg="2" xl="3">
                            { Object.keys(defaultValues.listStatus).map((value, i)  => (
                              <Label check key={value}>
                                <Input type="checkbox"  checked={defaultValues.listStatus[value]} onChange={handleChangeCheckBox}
                                placeholder={value} name={value}  />
                                {value}
                              </Label>
                            ))
                          }
                          </Row>
                        </Col>
                      </Row>
                    </FormGroup>


                    <FormGroup>
                      <Button type="submit" color="primary">Ajouter</Button>
                    </FormGroup>

              </Form>


              <h1> Ajouter un lieu existant </h1>
              {successAddLieuExistant && <Alert color="success">{successAddLieuExistant}</Alert>}
              {errorAddLieuExistant && <Alert color="danger">{errorAddLieuExistant}</Alert>}
              <FormGroup>
                <Label >Email</Label>
                <Input key="emailLieuExistant" type="text" placeholder="email du lieu existant" defaultValue={emailLieuExistant}
                onChange={e => {emailLieuExistant=e.target.value}} invalid={!!errorAddLieuExistant} />
              </FormGroup>
              <FormGroup>
                <Button color="primary" onClick={()=>validEmail(emailLieuExistant)}>Valider</Button>
              </FormGroup>
            </Col>
            <Col  md="9">
              <h1> Mes lieux</h1>
              {loadingDelete && <Alert color="info">En cours...</Alert>}
              {successDelete && <Alert color="success">{successDelete}</Alert>}
              {errorDelete && <Alert color="danger">{errorDelete}</Alert>}
              <Row xs="2" sm="4" md="2" lg="2" xl="3">
              { myLieux.map((lieu:any)  => (
                <Card>
                  <CardHeader className="text-center"><h1>{lieu.name}</h1></CardHeader>
                  <CardBody>
                    <CardTitle></CardTitle>
                    <CardSubtitle>Catégories : {lieu.categories && lieu.categories.length>=1 ?
                      lieu.categories.map((categorie:any)  => (<img style={{width: 60, height: 60}}  src={getCategoriesImage(categorie)} alt={categorie} title={categorie} />)):
                      <img style={{width: 60, height: 60}}  src={getCategoriesImage("non classé")} alt="Non classé" title="Non classé" />
                    }
                    </CardSubtitle>
                    <CardText>Adresse: {lieu.adress}</CardText>
                    <CardText>Horaire: {lieu.horaireOuverture}</CardText>
                    <Button onClick={()=>{deleteLieu(lieu.id)}}>Supprimer</Button>
                  </CardBody>
              </Card>
            ))}
              </Row>
            </Col>
          </Row>
        </Container>
      </React.Fragment>
    </Template>
  );
};

LieuxComponent.propTypes = {
  errorAddLieu: PropTypes.string,
  loadingAddLieu: PropTypes.bool,
  successAddLieu: PropTypes.string,
  defaultValues: PropTypes.shape({
    listStatus : PropTypes.shape({}).isRequired
  }),
  clickAddLieu: PropTypes.func.isRequired,
  errorDelete: PropTypes.string,
  loadingDelete: PropTypes.bool,
  successDelete: PropTypes.string,
  deleteLieu :PropTypes.func.isRequired,
  successAddLieuExistant:PropTypes.string,
  errorAddLieuExistant:PropTypes.string,
  emailLieuExistant:PropTypes.string,
  validEmail:PropTypes.func.isRequired,
};

LieuxComponent.defaultProps = {
  errorAddLieu: null,
  successAddLieu: null,
  loadingAddLieu: false,
  defaultValues: {
    name:"",
    adress:"",
    city : "",
    horaireOuverture:"",
    listStatus : []
  },
  errorDelete: null,
  loadingDelete: false,
  successDelete: null,
  myLieux:[],
  successAddLieuExistant:null,
  errorAddLieuExistant:null,
  emailLieuExistant:"",
};
export default LieuxComponent;
