import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Layout from '../../components/Commands/Commands';

class CommandsContainer extends Component {
  constructor(props) {
    super();

    this.state = {
      error: null, loading: false,
    };
  }

  componentDidMount = () =>this.props.fetchUser()


  updateStatus = async (command) => {
      this.setState({ loadingDelete: true, errorDelete: null});
   try{
     const {updateStatus} = this.props
     let mesageRetour = await updateStatus(command)
     this.setState({ loadingDelete: false,successDelete:"Commande supprimée avec succés."});
   }catch(e){
     this.setState({ loadingDelete: false, errorDelete: e.message });
   }
 }

  render = () => {
    const {purchases,sales} = this.props;
    const { loading, error ,successDelete,loadingDelete,errorDelete} = this.state;
    return (
      <Layout
        purchases={purchases}
        sales={sales}
        error={error}
        loading={loading}
        updateStatus={this.updateStatus}
      />
    );
  };
}
CommandsContainer.propTypes = {
  purchases:  PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  sales:  PropTypes.arrayOf(PropTypes.shape({})).isRequired,
};

CommandsContainer.defaultProps = {
  purchases: [],
  sales: [],
};

const mapStateToProps = (state,props) => ({
  purchases : state.commands.purchases,
  sales : state.commands.sales
});

const mapDispatchToProps = (dispatch) => ({
  fetchUser: dispatch.user.fetchUser,
  updateStatus: dispatch.commands.updateStatus,
});

export default connect(mapStateToProps, mapDispatchToProps)(CommandsContainer);
