import React,{ useState }  from 'react';
import PropTypes from 'prop-types';
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.min.css';
import './customTable.css'
import BootstrapTable from 'react-bootstrap-table-next';
import filterFactory, { selectFilter } from 'react-bootstrap-table2-filter';
import paginationFactory from 'react-bootstrap-table2-paginator';
import {
  Container,
  Row,
  Col,
  Alert,
  Table,
  Button
} from 'reactstrap';
import Template from '../Templates/Dashboard';
import Moment from 'moment';

const CommandsComponent = ({
  error, loading, purchases,sales,goToSimpleCommand,updateStatus
}) => {

  const columnsDetails = [{
    dataField: 'name',
    text: 'Nom',
  }, {
    dataField: 'quantity',
    text: 'Quantité',
  }, {
    dataField: 'price',
    text: 'Prix'
  },
  {
    dataField: 'status',
    text: 'Status'
  }];
  const selectOptions = {
    'En cours': 'En cours',
    'Acceptée': 'Acceptée',
    'Annulée': 'Annulée',
    'Livrée': 'Livrée'
  };
  const columnsAchats = [{
    dataField: 'sellerUserName',
    text: 'Nom',
    sort: true
  }, {
    dataField: 'quantity',
    text: 'Quantité',
    sort: true
  }, {
    dataField: 'price',
    text: 'Prix',
    sort: true
  },
  {
    dataField: 'status',
    text: 'Status',
    filter: selectFilter({
      options: selectOptions
    })
  },
  {
    dataField: 'creationDate',
    text: 'Date',
    formatter: (cell) => {
     return Moment(cell).format('DD/MM/YY HH:mm');
   },
   sort: true
  },
  {
    dataField: 'startDate',
    text: 'Min date de livraison',
    formatter: (cell) => {
     return Moment(cell).format('DD/MM/YY HH:mm');
   },
   sort: true
  },
  {
    dataField: 'endDate',
    text: 'Max date de livraison',
    formatter: (cell) => {
     return Moment(cell).format('DD/MM/YY HH:mm');
   },
   sort: true
  },
  {
          dataField: "databasePkey",
          text: "Actions",
          formatter: (cellContent: string, row: IMyColumnDefinition) => {
              return (<div>
                {row.status === "En cours" && <Button color="primary" onClick={()=>{updateStatus({id:row.id,status:"CANCELED"})}}>Annuler</Button>}{' '}
                {row.status === "Acceptée" && <Button color="primary" onClick={()=>{updateStatus({id:row.id,status:"DELIVERED"})}}>Reçu</Button>}{' '}
              </div>)
          },
      }
    ];

    const columnsVentes = [{
      dataField: 'sellerUserName',
      text: 'Nom',
      sort: true
    }, {
      dataField: 'quantity',
      text: 'Quantité',
      sort: true
    }, {
      dataField: 'price',
      text: 'Prix',
      sort: true
    },
    {
      dataField: 'status',
      text: 'Status',
      filter: selectFilter({
        options: selectOptions
      })
    },
    {
      dataField: 'creationDate',
      text: 'Date',
      formatter: (cell) => {
       return Moment(cell).format('DD/MM/YY HH:mm');
     },
     sort: true
    },
    {
      dataField: 'startDate',
      text: 'Min date de livraison',
      formatter: (cell) => {
       return Moment(cell).format('DD/MM/YY HH:mm');
     },
     sort: true
    },
    {
      dataField: 'endDate',
      text: 'Max date de livraison',
      formatter: (cell) => {
       return Moment(cell).format('DD/MM/YY HH:mm');
     },
     sort: true
    },
    {
            dataField: "databasePkey",
            text: "Actions",
            formatter: (cellContent: string, row: IMyColumnDefinition) => {
                return (<div>
                  {row.status === "Acceptée"  && <Button color="primary" onClick={()=>{updateStatus({id:row.id,status:"DELIVERED"})}}>Livrer</Button>}{' '}
                  {row.status === "En cours" && <Button color="primary" onClick={()=>{updateStatus({id:row.id,status:"ACCEPTED"})}}>Accepter</Button>}{' '}
                  {row.status === "En cours" && <Button color="primary" onClick={()=>{updateStatus({id:row.id,status:"CANCELED"})}}>Annuler</Button> }{' '}
                </div>)
            },
        }
      ];

      const expandRow = {
        renderer: row => (
          <div>
            <BootstrapTable bootstrap4 keyField='id' data={ row.commandProducts } columns={ columnsDetails }  />
          </div>
        )
      };




  return (
    <Template pageTitle="Produit">
      <React.Fragment>
        <Container fluid={true}>
          <Row  >
            <h1> Commandes Achetées</h1>
              <BootstrapTable bootstrap4 striped keyField='id' data={ purchases } columns={ columnsAchats } filter={ filterFactory() }  expandRow={ expandRow } pagination={ paginationFactory() } wrapperClasses="table-responsive"/>
          </Row>
          <Row>
          <h1> Commandes Vendues</h1>
          <BootstrapTable bootstrap4 striped keyField='id' data={ sales } columns={ columnsVentes } filter={ filterFactory() }  expandRow={ expandRow } pagination={ paginationFactory() } wrapperClasses="table-responsive"/>
          </Row>
        </Container>
      </React.Fragment>
    </Template>
  );
};

CommandsComponent.propTypes = {
  error: PropTypes.string,
  loading: PropTypes.bool,
  purchases : PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  sales : PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  updateStatus:PropTypes.func.isRequired
};

CommandsComponent.defaultProps = {
  commands: [],
  sales:[],
  error: null,
  loading: false
};

export default CommandsComponent;
