import React, { Component } from 'react';
import Notification  from 'react-web-notification';
import {subscribeNotif} from "src/lib/subscription";
const applicationServerPublicKey = 'BMaJhM1gvvOwfHHhaOS5M75sDaziWcCcLsDFiorLq41F7FRXybwAR6SbMkxZCl0Hs0Rzk6SCKY-bQDIp48JqvNw';

function urlB64ToUint8Array(base64String) {
  const padding = '='.repeat((4 - base64String.length % 4) % 4);
  const base64 = (base64String + padding)
    .replace(/\-/g, '+')
    .replace(/_/g, '/');

  const rawData = window.atob(base64);
  const outputArray = new Uint8Array(rawData.length);

  for (let i = 0; i < rawData.length; ++i) {
    outputArray[i] = rawData.charCodeAt(i);
  }
  return outputArray;
}


class ExternalNotification extends Component {

  constructor(props) {
   super(props);
   this.state = {
     ignore: true,
     title: ''
   };
  }

  /**
   * Step two: The serviceworker is registered (started) in the browser. Now we
   * need to check if push messages and notifications are supported in the browser
   */
  initialiseState2(){
      // Check if desktop notifications are supported
      if (!('showNotification' in ServiceWorkerRegistration.prototype)) {
          console.warn('Notifications aren\'t supported.');
          return;
      }
      // Check if user has disabled notifications
      // If a user has manually disabled notifications in his/her browser for
      // your page previously, they will need to MANUALLY go in and turn the
      // permission back on. In this statement you could show some UI element
      // telling the user how to do so.
      if (Notification.permission === 'denied') {
          console.warn('The user has blocked notifications.');
          return;
      }

      // Check is push API is supported
      if (!('PushManager' in window)) {
          console.warn('Push messaging isn\'t supported.');
          return;
      }
      const sendSubscriptionToServer = this.sendSubscriptionToServer.bind(this)
      const subscribe = this.subscribe.bind(this)
      navigator.serviceWorker.ready.then(function (serviceWorkerRegistration) {

          // Get the push notification subscription object
          serviceWorkerRegistration.pushManager.getSubscription().then(function (subscription) {

              // If this is the user's first visit we need to set up
              // a subscription to push notifications
              if (!subscription) {
                  subscribe();

                  return;
              }

              // Update the server state with the new subscription
              sendSubscriptionToServer(subscription);
          })
          .catch(function(err) {
              // Handle the error - show a notification in the GUI
              console.warn('Error during getSubscription()', err);
          });
      });
  }

  /**
   * Step three: Create a subscription. Contact the third party push server (for
   * example mozilla's push server) and generate a unique subscription for the
   * current browser.
   */
  subscribe(){
      const sendSubscriptionToServer = this.sendSubscriptionToServer.bind(this)
      navigator.serviceWorker.ready.then(function (serviceWorkerRegistration) {

          // Contact the third party push server. Which one is contacted by
          // pushManager is  configured internally in the browser, so we don't
          // need to worry about browser differences here.
          //
          // When .subscribe() is invoked, a notification will be shown in the
          // user's browser, asking the user to accept push notifications from
          // <yoursite.com>. This is why it is async and requires a catch.
          const applicationServerKey = urlB64ToUint8Array(applicationServerPublicKey);
          serviceWorkerRegistration.pushManager.subscribe({userVisibleOnly: true,applicationServerKey: applicationServerKey}).then(function (subscription) {

              // Update the server state with the new subscription
              return sendSubscriptionToServer(subscription);
          })
          .catch(function (e) {
              if (Notification.permission === 'denied') {
                  console.warn('Permission for Notifications was denied');
              } else {
                  console.error('Unable to subscribe to push.', e);
              }
          });
      });
  }

  /**
   * Step four: Send the generated subscription object to our server.
   */
  sendSubscriptionToServer(subscription){

      // Get public key and user auth from the subscription object
      var key = subscription.getKey ? subscription.getKey('p256dh') : '';
      var auth = subscription.getKey ? subscription.getKey('auth') : '';

      // This example uses the new fetch API. This is not supported in all
      // browsers yet.
      return subscribeNotif(subscription,key,auth)
  }


  unsubscribeUser(){
    this.props.swRegistration.pushManager.getSubscription()
    .then(function(subscription) {
      if (subscription) {
        return subscription.unsubscribe();
      }
    })
    .catch(function(error) {
      console.log('Error unsubscribing', error);
    })  
    .then(function() {
      //this.updateSubscriptionOnServer(null);
      console.log('User is unsubscribed.');
    });
  }



  handlePermissionGranted(){
   console.log('Permission Granted');
   this.setState({
     ignore: false
   });
  }
  handlePermissionDenied(){
   console.log('Permission Denied');
   this.setState({
     ignore: true
   });
  }
  handleNotSupported(){
   console.log('Web Notification not Supported');
   this.setState({
     ignore: true
   });
  }

  handleNotificationOnClick(e, tag){
   console.log(e, 'Notification clicked tag:' + tag);
  }

  handleNotificationOnError(e, tag){
   console.log(e, 'Notification error tag:' + tag);
  }

  handleNotificationOnClose(e, tag){
   console.log(e, 'Notification closed tag:' + tag);
  }

  handleNotificationOnShow(e, tag){
   this.playSound();
   console.log(e, 'Notification shown tag:' + tag);
  }

  playSound(){
   document.getElementById('sound').play();
  }

  handleButtonClick() {
   const now = Date.now();

   const title = 'React-Web-Notification' + now;
   const body = 'Hello' + new Date();
   const tag = now;
   const icon = 'http://mobilusoss.github.io/react-web-notification/example/Notifications_button_24.png';
   const options = {
     tag: tag,
     body: body,
     icon: icon,
     lang: 'en',
     dir: 'ltr',
     sound: 'command.mp3'
   }
   this.setState({
     title: title,
     options: options
   });
  }

  handleButtonClick2() {
   this.props.swRegistration.getNotifications({}).then(function(notifications) {
     console.log(notifications);
   });
  }

  componentDidMount = ()=>{
    //setTimeout(()=>{this.handleButtonClick()},3000)
    this.initialiseState2()
  }
  render = () => {
    return(
      <div>
        <Notification
         ignore={this.state.ignore && this.state.title !== ''}
         notSupported={this.handleNotSupported.bind(this)}
         onPermissionGranted={this.handlePermissionGranted.bind(this)}
         onPermissionDenied={this.handlePermissionDenied.bind(this)}
         onShow={this.handleNotificationOnShow.bind(this)}
         onClick={this.handleNotificationOnClick.bind(this)}
         onClose={this.handleNotificationOnClose.bind(this)}
         onError={this.handleNotificationOnError.bind(this)}
         timeout={5000}
         title={this.state.title}
         options={this.state.options}
         swRegistration={this.props.swRegistration}
       />
       <audio id='sound' preload='auto'>
          <source src='command.mp3' type='audio/mp3' />
          <source src='command.wav' type='audio/wav' />
          <embed hidden={true} autostart='false' loop={false} src='command.mp3' />
        </audio>
      </div>
    )
  };
}
export default ExternalNotification;
