import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { useForm } from 'react-hook-form';
import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  Alert,
  Form,
  FormGroup,
  Label,
  Input,
  Button
} from 'reactstrap';
import Template from '../Templates/Dashboard';
import {generalMessages, errorMessages } from 'src/constants/messages';



const Login = ({
  error, loading, success, defaultValues,onFormSubmit,goRegistration
}) => {
  const {
    register, handleSubmit, errors, setValue,
  } = useForm({ defaultValues });

  useEffect(() => {
    register({ name: 'username' }, { required: errorMessages.missingEmail });
    register({ name: 'password' }, { required: "mot de passe requis" });
  }, [register]);


  return (
    <Template pageTitle="Login">
      <React.Fragment>
    <Container>
        <Form onSubmit={handleSubmit(onFormSubmit)}>

              <h1>Inscription</h1>


          {error && <Alert color="danger">{error} </Alert>}
          {loading && <Alert color="info">{generalMessages.loadingMsg} </Alert>}
          {success && <Alert color="success">{success} </Alert>}


            <FormGroup >
              <Label>Nom</Label>
              {errors.username && <Alert color="danger">{errors.username.message}</Alert>}
              <Input
                type="text"
                name="username"
                autoCapitalize="none"
                placeholder="Alain Soral"
                defaultValue={defaultValues.username || ''}
                onChange={(e) => setValue('username', e.target.value)}
              />
            </FormGroup>

            <FormGroup >
              <Label>Mot de passe</Label>
                {errors.password && <Alert color="danger">{errors.password.message}</Alert>}
              <Input
                type="text"
                name="password"
                autoCapitalize="none"
                placeholder="quenelle"
                defaultValue={defaultValues.password || ''}
                onChange={(e) => setValue('password', e.target.value)}
              />
            </FormGroup>

            <FormGroup >

            <Button type="submit" disabled={loading} color="primary">{loading ? generalMessages.loadingBtn : generalMessages.connectBtn }</Button>

            </FormGroup>
            </Form>

            <Button onClick={goRegistration} disabled={loading} color="primary">{loading ? generalMessages.loadingBtn : generalMessages.savingBtn}</Button>

    </Container>
    </React.Fragment>
  </Template>
  );
};





Login.propTypes = {
  error: PropTypes.string,
  loading: PropTypes.bool,
  success: PropTypes.string,
  defaultValues: PropTypes.shape({
    username : PropTypes.string,
    password : PropTypes.string,
  }),
  onFormSubmit: PropTypes.func,
  goRegistration: PropTypes.func
};

Login.defaultProps = {
  error: null,
  success: null,
  loading: false,
  defaultValues: {
    username : "a",
    password : "a",
  },
};

export default Login;
