import React  ,{ useEffect,useState } from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faAddressCard,faPhone,faAt,faBusinessTime} from '@fortawesome/free-solid-svg-icons'
import DatePicker, { registerLocale } from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

import fr from 'date-fns/locale/fr';

import {
  Container,
  Row,
  Col,
Table,
  Card,CardSubtitle,CardText,
  CardBody,CardHeader,
  Alert,
  Input,
  Label,Form,FormGroup,Button,Modal,ModalHeader,ModalBody,ModalFooter,ListGroup,ListGroupItem
} from 'reactstrap';
import {getCategoriesImage} from 'src/lib/images'
import { useForm } from 'react-hook-form';
import Template from '../Templates/Dashboard';
import { Map, TileLayer, Marker, Popup } from "react-leaflet";
import 'leaflet/dist/leaflet.css';
import L from 'leaflet';

L.Icon.Default.mergeOptions({
    iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
    iconUrl: require('leaflet/dist/images/marker-icon.png'),
    shadowUrl: require('leaflet/dist/images/marker-shadow.png')
});

registerLocale("fr", fr);

function myIcom(name){
  const icon = L.icon({
    iconRetinaUrl: getCategoriesImage(name),
    iconUrl: getCategoriesImage(name),
    iconSize: [30, 30],
  })
  return icon
}

const MapComponent = ({
  errorSearch,listCategories, loadingSearch,successSearch,clickSearch, lieux,user, reFetch,showFarmer,defaultValuesSearch,listVilleResultSearch,selectCityAndInitDataMap,showUser,userSelected,makeCommand,successCommand,errorCommand,loadingCommand
}) => {
  const { register, handleSubmit, errors , setValue} = useForm({defaultValuesSearch});

  useEffect(() => {
    register({ name: 'ville' }, { required: "ville requise"  });
    register({ name: 'productTypes' });
  }, [register]);

  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());


  const [checkedItems, setCheckedItems] = useState(defaultValuesSearch.productTypes);
    const handleChangeCheckBox = event => {
      defaultValuesSearch.productTypes[event.target.name]= event.target.checked
      setCheckedItems({
        ...checkedItems,
        [event.target.name]: event.target.checked
      });
    };
    const clickSearchCmp=(data)=>{
      data.productTypes = defaultValuesSearch.productTypes
      clickSearch(data)
    }
    const [modal, setModal] = useState(true);
    const [modal2, setModal2] = useState(false);
    const toggle = () => setModal(!modal);
    const toggle2 = () => setModal2(!modal2);
    let imgNum = 0;
  return (
    <Template pageTitle="Map">
      <React.Fragment>
        <Container fluid={true}>

        {userSelected && <Modal isOpen={modal2} toggle={toggle2} >
          <ModalHeader key="modal2" toggle={toggle2} >Faites votre commande</ModalHeader>
          <ModalBody>
          {successCommand && <Alert color="success">{successCommand}</Alert>}
          {errorCommand && <Alert color="danger">{errorCommand}</Alert>}
          {loadingCommand && <Alert color="info">En cours...</Alert>}

            <Table>
              <thead>
                <tr>
                  <th>Nom</th>
                  <th>Prix</th>
                  <th>Quantité</th>
                  <th>Quantité voulue</th>
                </tr>
              </thead>
              <tbody>
                { userSelected.products.map((product:any)  => (
                  <tr key={product.id}>
                    <td>{product.name}</td>
                    <td>{product.price} €/kg</td>
                    <td>{product.quantity} kg</td>
                    <td><Input  type="number" placeholder="Quantité" defaultValue={product.boughtQuantity || '0'}
                    onChange={e => product.boughtQuantity=e.target.value}  /></td>
                  </tr>
                ))}
              </tbody>
            </Table>

              <Row>
                <FormGroup>
                  <Label >Horaire de récupération de la commande</Label><br/>
                  Entre{'  '}
                  <DatePicker
                    name="startDate"
                    selected={startDate}
                    onChange={date => {userSelected.startDate=date;setStartDate(date)}}
                    selectsStart
                    timeFormat="HH:mm"
                    showTimeSelect
                    startDate={startDate}
                    endDate={endDate}
                    dateFormat="dd/MM/yyyy HH:mm"
                    locale="fr"
                  />{'  '}et{'  '}
                  <DatePicker
                    name="endDate"
                    selected={endDate}
                    onChange={date => {userSelected.endDate=date;setEndDate(date)}}
                    selectsEnd
                    showTimeSelect
                    timeFormat="HH:mm"
                    startDate={startDate}
                    endDate={endDate}
                    minDate={startDate}
                    dateFormat="dd/MM/yyyy HH:mm"
                    locale="fr"
                  />
                </FormGroup>
              </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" disabled={loadingCommand} onClick={() => makeCommand(userSelected)}>{loadingCommand?'En cours...':'Commander'}</Button>
            <Button color="primary" onClick={toggle2}>Annuler</Button>
          </ModalFooter>
        </Modal>
      }

          {listVilleResultSearch && <Modal isOpen={modal} toggle={toggle} >
            <ModalHeader key="modal1" toggle={toggle} >Sélectionner la ville de votre choix</ModalHeader>
            <ModalBody>
            <ListGroup>
            { listVilleResultSearch.map((ville:any)  => (
                <ListGroupItem style={{cursor: 'pointer'}} action onClick={()=>selectCityAndInitDataMap(ville)}>{ville.nom +"("+ville.code.substring(0,2)+")"}</ListGroupItem>
            ))}
            </ListGroup>
            </ModalBody>
            <ModalFooter>
              <Button color="primary" onClick={toggle}>Annuler</Button>
            </ModalFooter>
          </Modal>
        }
          <Row  sm="1" md="2">
            <Col  md="3">
              <Form onSubmit={handleSubmit(clickSearchCmp)}>
                <Row >
                  <Col xs={1} />
                  <Col >
                    {successSearch && <Alert color="success">{successSearch}</Alert>}
                    {errorSearch && <Alert color="danger">{errorSearch}</Alert>}
                    <FormGroup>
                      <Label >Zone de recherche</Label>
                      <Input key="ville" type="search" placeholder="ville" name="ville" defaultValue={defaultValuesSearch.ville.name || ''}
                      onChange={e => setValue("ville",e.target.value)} invalid={!!errors.ville} />
                    </FormGroup>
                    {errors.ville && <Alert color="danger">{errors.ville.message}</Alert>}
                    <FormGroup>
                      <Label >Type de produit recherché</Label>
                      <Row xs="2" sm="4" md="2" lg="2" xl="3">
                        { Object.keys(defaultValuesSearch.productTypes).map((value, i)  => (
                          <Label check key={value}>
                            <Input type="checkbox"  checked={defaultValuesSearch.productTypes[value]} onChange={handleChangeCheckBox}
                            placeholder={value} name={value}  />
                            {value}
                          </Label>
                        ))
                      }
                      </Row>
                    </FormGroup>
                    <FormGroup>
                      <Button type="submit" color="primary">Rechercher</Button>
                    </FormGroup>
                  </Col>
                </Row>
              </Form>
            </Col>
            <Col  md="9">
            {(defaultValuesSearch.ville.centre || user.choixCentreMap) &&
              <Map center={defaultValuesSearch.ville && defaultValuesSearch.ville.centre.coordinates.length===2 ?
                {lng:defaultValuesSearch.ville.centre.coordinates[0],lat:defaultValuesSearch.ville.centre.coordinates[1]}: user.choixCentreMap}
                zoom={12} style={{flex:1,width: '100%',minHeight:'600px',}}>
                <TileLayer
                  attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                  url='https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png'
                />
                {lieux.map((lieu:any)  => (
                  <Marker icon={myIcom(lieu.categories[0])}  key={lieu.id} position={{lat:lieu.lat,lng:lieu.lng}}  >
                    <Popup>
                      <Card>
                        <CardHeader className="text-center"><h1>{lieu.name}</h1></CardHeader>
                        <CardBody>
                          <CardSubtitle key={"subtitle"+lieu.id}>{lieu.categories && lieu.categories.length>=1 ?
                            lieu.categories.map((categorie:any,index)  => (<img key={"img"+imgNum++} style={{width: 60, height: 60}}  src={getCategoriesImage(categorie)} alt={categorie} title={categorie} />)):
                            <img key={"img"+imgNum++} style={{width: 60, height: 60}}  src={getCategoriesImage("non classé")} alt="Non classé" title="Non classé" />
                          }
                          </CardSubtitle>

                          {lieu.adress && <CardText><FontAwesomeIcon size="2x" icon={faAddressCard} />
                          {' '}
                          {lieu.adress}</CardText>}
                          {lieu.horaireOuverture && <CardText><FontAwesomeIcon size="2x" icon={faBusinessTime} />
                          {' '}
                          {lieu.horaireOuverture}</CardText>}
                          {lieu.email && <CardText><FontAwesomeIcon size="2x" icon={faAt} />
                          {' '}
                          {lieu.email}</CardText>}
                          {lieu.phone && <CardText><FontAwesomeIcon size="2x" icon={faPhone} />
                          {' '}
                          {lieu.phone}</CardText>}
                          {lieu.userDTO && <Button color="primary" onClick={()=>{showUser(lieu.userDTO);toggle2();}}>Voir Produits</Button>}

                        </CardBody>
                    </Card>
                    </Popup>
                  </Marker>
                      ))
                   }


              </Map>
              }
            </Col>
          </Row>
        </Container>
      </React.Fragment>
    </Template>
  );
};

MapComponent.propTypes = {
  successSearch : PropTypes.string,
  errorSearch: PropTypes.string,
  loadingSearch: PropTypes.bool,
  defaultValuesSearch: PropTypes.shape({
    ville: PropTypes.shape({}),
    productTypes : PropTypes.shape(),
    startDate : PropTypes.instanceOf(Date),
    endDate : PropTypes.instanceOf(Date),
  }),
  listCategories:PropTypes.shape(),
  clickSearch:PropTypes.func,
  user: PropTypes.shape(),
  lieux : PropTypes.arrayOf(PropTypes.shape()),
  reFetch: PropTypes.func,
  showFarmer : PropTypes.func,
  selectCityAndInitDataMap : PropTypes.func,
  showUser:PropTypes.func.isRequired,
  makeCommand : PropTypes.func.isRequired,
  successCommand : PropTypes.string,
  errorCommand: PropTypes.string,
  loadingCommand: PropTypes.bool,

};

MapComponent.defaultProps = {
  successSearch: null,
  errorSearch: null,
  loadingSearch: false,
  defaultValuesSearch: {
    ville : "Verlinghem",
    productTypes : {"Legume":true,"Viande":true,"Poisson":true,"Boisson":true,"Spiritueux":true}
  },
  clickSearch : null,
  selectCityAndInitDataMap:null,
  user: {},
  lieux : [],
  reFetch: null,
  showFarmer : null,
  successCommand : null,
  errorCommand: null,
  loadingCommand: false,
};

export default MapComponent;
