/**
 * Create a readable error message for the front-end user
 */
export default (error) => {
  /*
    For an error response like:
    {
      "message": "422 Unprocessable Entity",
      "errors": {
        "email": [
          "The email must be a valid email address."
        ]
      }
    }
   */
  if (error && error.errors) {
    let errors = '';
    Object.entries(error.errors).forEach((v) => {
      errors += v[1].join(', ');
    });
    return Error(errors);
  }

  if(error && error.status == 403) {
    if(error.message == "Forbidden") throw new Error("Vous n'êtes pas authorizé à effectuer cette action. Veuillez consulter l'aide en haut à droite pour de plus amples informations.")
    throw new Error("Vous n'êtes pas authentifié. Veuillez vous connecter.")
  }

  if (error && error.message) {
    console.log(error.message)
    return new Error(error.message);
  }

  // When an Error - return the error
  if (error instanceof Error) {
    return error;
  }

  // Otherwise create an error
  return new Error('Uh oh - something happened');
};
