/* global document */
import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { BrowserRouter as Router } from 'react-router-dom';
import { PersistGate } from 'redux-persist/es/integration/react';

import configureStore from 'src/store/index';
import Routes from './routes/index';

// Components
import Loading from './components/UI/Loading';

// Load css
import './assets/styles/style.scss';
import InternalNotification from './containers/Notification/InternalNotification'
import ExternalNotification from './containers/Notification/ExternalNotification'


const { persistor, store, dispatch } = configureStore();
// persistor.purge(); // Debug to clear persist


class Root extends React.Component {
  render() {
    return (
      <Provider store={store}>
        <PersistGate loading={<Loading />} persistor={persistor}>
          <Router dispatch={dispatch} store={store}>
            <Routes />
          </Router>
        </PersistGate>
        <InternalNotification/>
        {this.props.swRegistration && <ExternalNotification swRegistration={this.props.swRegistration}/>}
      </Provider>
    );
  }
}

/**
 * Step one: run a function on load (or whenever is appropriate for you)
 * Function run on load sets up the service worker if it is supported in the
 * browser. Requires a serviceworker in a `sw.js`. This file contains what will
 * happen when we receive a push notification.
 * If you are using webpack, see the section below.
 */
 if('serviceWorker' in navigator) {
   navigator.serviceWorker.register('sw.js')
   .then(function(registration) {
     ReactDOM.render(<Root swRegistration={registration}/>, document.getElementById('root'));
   }).catch(function(error) {
     ReactDOM.render(<Root />, document.getElementById('root'));
   })
 }else{
   ReactDOM.render(<Root />, document.getElementById('root'));
 }
