import moment from 'moment';
import HandleErrorMessage from '../lib/format-error-messages';
import initialState from '../store/commands';
import Config from '../constants/config';
import { getFeaturedImageUrl } from '../lib/images';
import { ucfirst, stripHtml } from '../lib/string';
import { errorMessages, successMessages } from '../constants/messages';
import pagination from '../lib/pagination';
import axios from '../lib/api';
import config from '../constants/config';
export default {
  namespace: 'commands',

  /**
   *  Initial state
   */
  state: initialState,

  /**
   * Effects/Actions
   */
  effects: (dispatch) => ({
    /**
     * Save date to redux store
     * @param {obj} data
     * @returns {Promise[obj]}
     */
    async saveAll(user) {
      try {
        let command = {commandProducts:[]};
        let price =0,quantity=0;

        user.products.forEach((product)=>{
          if(!!product.boughtQuantity  ){
            if(product.quantity < parseInt(product.boughtQuantity)){
              throw new Error(errorMessages.boughtQuantitySupQUantity)
            }
            command.commandProducts.push({
              idProduct : product.id,
              quantity : product.boughtQuantity,
              price : product.price,
            })
            price = price+ parseFloat(product.price)
            quantity = quantity + parseFloat(product.boughtQuantity)
          }
        });
        if(command.commandProducts.length == 0 ){
          throw new Error(errorMessages.needProductDifferent0ToCommand)
        }
        command.idSeller=user.id
        command.price = price
        command.quantity = quantity
        if(user.endDate<user.startDate){
          throw new Error(errorMessages.endDateSupToStartDate)
        }
        command.startDate = user.startDate
        command.endDate = user.endDate


        let response = await axios.post("/command/save", command);
        const { data } = response;
        if (!data) {
          throw new Error(errorMessages.usersEmpty );
        }
        dispatch.commands.addToPurchases(command);
        return successMessages.commandSaved; // Message for the UI
      } catch (error) {
        throw HandleErrorMessage(error);
      }
    },
    async updateStatus(command) {
      try {
        console.log("/command/"+command.id+"/status/"+command.status)
        let response = await axios.get( "/command/" + command.id + "/status/"+ command.status);
        const { data } = response;
        if (!data) {
          throw new Error(errorMessages.updateStatus );
        }
        dispatch.commands.replaceCommandStatus(command);
        return successMessages.commandSaved;
      } catch (error) {
        throw HandleErrorMessage(error);
      }
    }
  }),


  /**
   * Reducers
   */
  reducers: {
    /**
     * Save form data
     * @param {obj} state
     * @param {obj} payload
     */
    addToPurchases(state, payload) {
      state.purchases = [...state.purchases].concat(payload);
      return {...state};
    },

    initReduxCommands(state,purchases,sales){
      state.purchases=purchases;
      state.sales=sales;
      return {...state};
    },
    updateCurrentCommand(state,currentCommand) {
      state.currentCommand = currentCommand;
      return {...state};
    },
    replaceCommandStatus(state,commandToRemove){
      return {
        ...state,
        purchases: state.purchases.filter((command) => command.id !== commandToRemove.id),
        sales: state.sales.map((command)=> {if(commandToRemove.id===command.id){command.status=commandToRemove.status;}return command;})
      };
    }

  },
};
