import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Products from '../../components/Products/Products';


class ProductContainer extends Component {
  constructor() {
    super();
    this.state = { loadingAddProduct: true, errorAddProduct: null,errorDelete: null, successDelete: null, loadingDelete: false };
  }

  componentDidMount = () =>this.props.fetchUser()

  onAddProduct = async (data) => {
    this.setState({ success: null, errorAddProduct: null, loadingAddProduct: true });
    const { onAddProduct } = this.props;
    try {
      const success = await onAddProduct(data);
      this.setState({ successAddProduct:success, errorAddProduct: null, loadingAddProduct: false });
      setTimeout(() => {this.setState({ successAddProduct: null})}, 2000);
    } catch (error) {
      this.setState({ loadingAddProduct: false, successAddProduct: null, errorAddProduct: error.message });
    }
  }

  deleteProduct = async (id) => {
    const { deleteProduct} = this.props;
    this.setState({ errorDelete: null, successDelete: null, loadingDelete: true });
    try {
      const successMessage = await deleteProduct(id);
      this.setState({ errorDelete: null, successDelete: successMessage, loadingDelete: false});
      setTimeout(() => {this.setState({ successDelete: null})}, 2000);
    } catch (error) {
      this.setState({ errorDelete: error.message, successDelete: null, loadingDelete: false  });
    }
  }



  render = () => {
    const { errorAddProduct, loadingAddProduct, successAddProduct,errorDelete, successDelete, loadingDelete } = this.state;
    const {  listStatus,myProducts} = this.props;
    return <Products
        errorAddProduct={errorAddProduct}
        loadingAddProduct={loadingAddProduct}
        successAddProduct={successAddProduct}
        clickAddProduct={this.onAddProduct}
        defaultValues={{name:"",price:"",quantity : "",listStatus:listStatus.reduce(function(map, obj) {map[obj] = false;return map;}, {})}}
        errorDelete={errorDelete}
        successDelete={successDelete}
        loadingDelete={loadingDelete}
        deleteProduct={this.deleteProduct}
        modifyProduct={this.modifyProduct}
        myProducts={myProducts}
     />;
  };
}

ProductContainer.propTypes = {
  onAddProduct: PropTypes.func.isRequired,
  listStatus : PropTypes.arrayOf(PropTypes.string).isRequired
};

ProductContainer.defaultProps = {

};

const mapStateToProps = (state) => ({
  listStatus : state.products.listStatus,
  myProducts : state.products.myProducts,
});

const mapDispatchToProps = (dispatch) => ({
  onAddProduct: dispatch.products.save,
  deleteProduct: dispatch.products.deleteProduct,
  fetchUser: dispatch.user.fetchUser,
});

export default connect(mapStateToProps, mapDispatchToProps)(ProductContainer);
