import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Lieux from '../../components/Lieux/Lieux';


class LieuxContainer extends Component {
  constructor() {
    super();
    this.state = { loadingAddLieu: true, errorAddLieu: null,errorDelete: null, successDelete: null, loadingDelete: false,
    errorAddLieuExistant: null, successAddLieuExistant: null, loadingAddLieuExistant: false };
  }

  componentDidMount = () =>this.props.fetchUser()

  onAddLieu = async (data) => {
    this.setState({ success: null, errorAddLieu: null, loadingAddLieu: true });
    const { onAddLieu } = this.props;
    try {
      const success = await onAddLieu(data);
      this.setState({ successAddLieu:success, errorAddLieu: null, loadingAddLieu: false });
      setTimeout(() => {this.setState({ successAddLieu: null})}, 2000);
    } catch (error) {
      this.setState({ loadingAddLieu: false, successAddLieu: null, errorAddLieu: error.message });
    }
  }

  deleteLieu = async (id) => {
    const { deleteLieu} = this.props;
    this.setState({ errorDelete: null, successDelete: null, loadingDelete: true });
    try {
      const successMessage = await deleteLieu(id);
      this.setState({ errorDelete: null, successDelete: successMessage, loadingDelete: false});
      setTimeout(() => {this.setState({ successDelete: null})}, 2000);
    } catch (error) {
      this.setState({ errorDelete: error.message, successDelete: null, loadingDelete: false  });
    }
  }

  validEmail  = async (email) => {
    const { validEmail} = this.props;
    this.setState({ errorAddLieuExistant: null, successAddLieuExistant: null, loadingAddLieuExistant: true });
    try {
      const successMessage = await validEmail(email);
      this.setState({ errorAddLieuExistant: null, successAddLieuExistant: successMessage, loadingAddLieuExistant: false});
      setTimeout(() => {this.setState({ successAddLieuExistant: null})}, 2000);
    } catch (error) {
      this.setState({ errorAddLieuExistant: error.message, successAddLieuExistant: null, loadingAddLieuExistant: false  });
    }
  }

  render = () => {
    const { errorAddLieu, loadingAddLieu, successAddLieu,errorDelete, successDelete, loadingDelete,successAddLieuExistant,errorAddLieuExistant } = this.state;
    const {  listStatus,myLieux} = this.props;
    return <Lieux
        errorAddLieu={errorAddLieu}
        loadingAddLieu={loadingAddLieu}
        successAddLieu={successAddLieu}
        clickAddLieu={this.onAddLieu}
        defaultValues={{name:"",  adress:"",  city : "",horaireOuverture:"",listStatus:listStatus.reduce(function(map, obj) {map[obj] = false;return map;}, {})}}
        errorDelete={errorDelete}
        successDelete={successDelete}
        loadingDelete={loadingDelete}
        deleteLieu={this.deleteLieu}
        modifyLieu={this.modifyLieu}
        myLieux={myLieux}
        successAddLieuExistant={successAddLieuExistant}
        errorAddLieuExistant={errorAddLieuExistant}
        emailLieuExistant=""
        validEmail={this.validEmail}
     />;
  };
}

LieuxContainer.propTypes = {
  onAddLieu: PropTypes.func.isRequired,
  listStatus : PropTypes.arrayOf(PropTypes.string).isRequired
};

LieuxContainer.defaultProps = {

};

const mapStateToProps = (state) => ({
  listStatus : state.products.listStatus,
  myLieux : state.lieux.me,
});

const mapDispatchToProps = (dispatch) => ({
  onAddLieu: dispatch.lieux.save,
  fetchUser: dispatch.user.fetchUser,
  deleteLieu: dispatch.lieux.delete,
  validEmail : dispatch.lieux.validEmail
});

export default connect(mapStateToProps, mapDispatchToProps)(LieuxContainer);
