import React from 'react';
import { Switch, Route } from 'react-router-dom';
import CustomRoute from './Route';
import PrivateRoute from './PrivateRoute';

// Containers
import {MapContainer,LoginContainer,RegistrationContainer,ProductsContainer,LieuxContainer,CommandsContainer} from '../containers';
// Components
import Error from '../components/UI/Error';

/**
 * All of the routes
 */

const Index = () => (
  <Switch>
    <Route path="/" exact component={MapContainer} />
    <CustomRoute path="/login" component={LoginContainer} />
    <CustomRoute path="/inscription" component={RegistrationContainer} />
    <CustomRoute path="/map" component={MapContainer} />
    <PrivateRoute path="/produit" component={ProductsContainer} />
    <PrivateRoute path="/lieux" component={LieuxContainer} />
    <PrivateRoute path="/commandes" component={CommandsContainer} />

    {/* 404 */}
    <Route
      render={(props) => (
        <Error {...props} title="404" content="Sorry, the route you requested does not exist" />
      )}
    />
  </Switch>
);

export default Index;
