import React  ,{ useEffect,useState } from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faMailchimp} from '@fortawesome/free-brands-svg-icons'

import {
  Container,
  Row,
  Col,

  Card,CardTitle,CardSubtitle,CardText,CardHeader,CardFooter,
  CardBody,
  Alert,
  Input,
  Label,Form,FormGroup,Button,Modal,ModalHeader,ModalBody,ModalFooter,ListGroup,ListGroupItem
} from 'reactstrap';
import { useForm } from 'react-hook-form';
import Template from '../Templates/Dashboard';
import {getCategoriesImage} from 'src/lib/images'

const ProductComponent = ({
  errorAddProduct, loadingAddProduct, successAddProduct, clickAddProduct, defaultValues,errorDelete, successDelete,  loadingDelete,  deleteProduct,myProducts
}) => {
  const { register, handleSubmit, errors , setValue} = useForm({defaultValues});

  useEffect(() => {
    register({ name: 'name' }, { required: "nom requis" });
    register({ name: 'price' }, { required: "price required" ,min: {value: 0,message: 'prix sup 0'   } });
    register({ name: 'quantity' }, { required: "quantity required" ,min: {value: 0,message: 'quantity sup 0'   } });
    register({ name: 'listStatus' })
  }, [register]);

  const [checkedItems, setCheckedItems] = useState(defaultValues.listStatus);
    const handleChangeCheckBox = event => {
      defaultValues.listStatus[event.target.name]= event.target.checked
      setCheckedItems({
        ...checkedItems,
        [event.target.name]: event.target.checked
      });
      console.log(defaultValues)
    };

const updateListStatus=(data)=>{
  console.log(data)
  data.listStatus = defaultValues.listStatus
  clickAddProduct(data)
}
  return (
    <Template pageTitle="Produit">
      <React.Fragment>
        <Container fluid={true}>
          <Row  sm="1" md="2">
            <Col  md="3">
              <Form onSubmit={handleSubmit(updateListStatus)}>
                    {successAddProduct && <Alert color="success">{successAddProduct}</Alert>}
                    {errorAddProduct && <Alert color="danger">{errorAddProduct}</Alert>}

                    <FormGroup>
                      <h1> Ajouter un produit</h1>
                      <Label >Nom</Label>
                      {errors.name && <Alert color="danger">{errors.name.message}</Alert>}
                      <Input key="name" type="text" placeholder="name" name="name" defaultValue={defaultValues.name }
                      onChange={e => setValue("name",e.target.value)} invalid={!!errors.name} />
                    </FormGroup>


                    <FormGroup>
                      <Label >Prix</Label>
                      {errors.price && <Alert color="danger">{errors.price.message}</Alert>}
                      <Input key="price" type="text" placeholder="price" name="price" defaultValue={defaultValues.price }
                      onChange={e => setValue("price",e.target.value)} invalid={!!errors.price} />
                    </FormGroup>

                    <FormGroup>
                      <Label >Quantité disponible</Label>
                      {errors.quantity && <Alert color="danger">{errors.quantity.message}</Alert>}
                      <Input key="quantity" type="text" placeholder="quantity" name="quantity" defaultValue={defaultValues.quantity}
                      onChange={e => setValue("quantity",e.target.value)} invalid={!!errors.quantity} />
                    </FormGroup>

                    <FormGroup>
                      <Row >
                        <Col xs={1} />
                        <Col>
                          <Label >Type du produit</Label>
                          <Row xs="2" sm="4" md="2" lg="2" xl="3">
                            { Object.keys(defaultValues.listStatus).map((value, i)  => (
                              <Label check key={value}>
                                <Input type="checkbox"  checked={defaultValues.listStatus[value]} onChange={handleChangeCheckBox}
                                placeholder={value} name={value}  />
                                {value}
                              </Label>
                            ))
                          }
                          </Row>
                        </Col>
                      </Row>
                    </FormGroup>


                    <FormGroup>
                      <Button type="submit" color="primary">Ajouter</Button>
                    </FormGroup>

              </Form>
            </Col>
            <Col  md="9">
              <h1> Mes produits</h1>
              {loadingDelete && <Alert color="info">En cours...</Alert>}
              {successDelete && <Alert color="success">{successDelete}</Alert>}
              {errorDelete && <Alert color="danger">{errorDelete}</Alert>}
              <Row xs="2" sm="4" md="2" lg="2" xl="3">
              { myProducts.map((product:any)  => (
                <Card>
                  <CardHeader className="text-center"><h1>{product.name}</h1></CardHeader>
                  <CardBody>
                    <CardTitle></CardTitle>
                    <CardSubtitle>Catégories : {product.categories && product.categories.length>=1 ?
                      product.categories.map((categorie:any)  => (<img style={{width: 60, height: 60}}  src={getCategoriesImage(categorie)} alt={categorie} title={categorie} />)):
                      <img style={{width: 60, height: 60}}  src={getCategoriesImage("non classé")} alt="Non classé" title="Non classé" />
                    }
                    </CardSubtitle>
                    <CardText>Quantité disponible: {product.quantity} kg</CardText>
                    <CardText>Prix : {product.price} €/kg</CardText>

                    <Button onClick={()=>{deleteProduct(product.id)}}>Supprimer</Button>
                  </CardBody>
              </Card>
            ))}
              </Row>
            </Col>
          </Row>
        </Container>
      </React.Fragment>
    </Template>
  );
};

ProductComponent.propTypes = {
  errorAddProduct: PropTypes.string,
  loadingAddProduct: PropTypes.bool,
  successAddProduct: PropTypes.string,
  defaultValues: PropTypes.shape({
    listStatus : PropTypes.shape({}).isRequired
  }),
  clickAddProduct: PropTypes.func.isRequired,
  errorDelete: PropTypes.string,
  loadingDelete: PropTypes.bool,
  successDelete: PropTypes.string,
  deleteProduct :PropTypes.func.isRequired,


};

ProductComponent.defaultProps = {
  errorAddProduct: null,
  successAddProduct: null,
  loadingAddProduct: false,
  defaultValues: {
    name:"",
    price:"",
    quantity : "",
    listStatus : []
  },
  errorDelete: null,
  loadingDelete: false,
  successDelete: null,
  myProducts:[]
};
export default ProductComponent;
