import moment from 'moment';
import axios from '../lib/api';
import HandleErrorMessage from '../lib/format-error-messages';
import initialState from '../store/user';
import Config from '../constants/config';
import { getFeaturedImageUrl } from '../lib/images';
import { errorMessages, successMessages } from '../constants/messages';
import deviceStorage from '../lib/device-storage';
import config from '../constants/config';
import { connect } from 'react-redux';

export default {
  namespace: 'user',

  /**
   *  Initial state
   */
  state: initialState,

  /**
   * Effects/Actions
   */
  effects: (dispatch) => ({

    /**
     * Get a single item from the API
     * @returns {Promise[obj]}
     */
    async login(userPassword) {
      try {
        let response = await axios.post("/login", userPassword)
        const { data } = response;
        if (!data) {
          throw new Error(errorMessages.usersEmpty )
        }
        dispatch.products.initReduxProducts(data.products)
        dispatch.commands.initReduxCommands(data.purchases,data.sales)
        data.products = null
        data.purchases = null
        data.sales = null
        this.replaceUser(data)
        deviceStorage.saveKey(Config.keyToken,data.token)
        deviceStorage.saveKey(Config.keyId,data.id.toString())
        return data
      } catch (error) {
        throw HandleErrorMessage(error);
      }
    },

    /**
     * Get a single item from the API
     * @returns {Promise[obj]}
     */
    async fetchUser() {
      try {
        const response = await axios.get("/user")
        const { data } = response;
        if (!data) {
          throw new Error(errorMessages.usersEmpty )
        }
        dispatch.products.initReduxProducts(data.products)
        dispatch.commands.initReduxCommands(data.purchases,data.sales)
        dispatch.lieux.replaceMyLieux(data.lieux)
        this.replaceUser(data)
        return data
      } catch (error) {
        throw HandleErrorMessage(error);
      }
    }
  }),

  /**
   * Reducers
   */
  reducers: {
    replaceUser(state, payload) {
      let newUser = {choixCentreMap:state.choixCentreMap,username:payload.username,id:payload.id,token:payload.token,lat:payload.lat,lng:payload.lng,email:payload.email,phone:payload.phone}
      return {...newUser};
    },

    replaceChoixCentre(state,payload){
      state.choixCentreMap = payload;
      return {...state}
    }

  },
};
