import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { useForm } from 'react-hook-form';
import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  Alert,
  Form,
  FormGroup,
  Label,
  Input,
  Button,
} from 'reactstrap';
import Template from '../Templates/Dashboard';
import { generalMessages, errorMessages } from 'src/constants/messages';



const Registration = ({
  error, loading, success, defaultValues,onFormSubmit
}) => {
  const {register, handleSubmit, errors, setValue} = useForm({ defaultValues });


  useEffect(() => {
    register({ name: 'username' }, { required: "username required" });
    register({ name: 'email' }, { required: "email required" ,pattern:{value: /^\S+@\S+$/i, message :"email pattern not good"}});
    register({ name: 'password' }, { required: "mot de passe required" });
    register({ name: 'adress' }, { required: "adress required" });
    register({ name: 'city' }, { required: "city required" });
    register({ name: 'phone' }, { required: "phone required",pattern:{value:  /\d{10}/i, message :"Tél doit faire 10 caractères"}} );
  }, [register]);

  console.log(errors)
  return (
    <Template pageTitle="Inscription">
      <React.Fragment>
    <Container>
        <h1>S'inscrire</h1>

        {error && <Alert color="danger">{error} </Alert>}
        {loading && <Alert color="info">{generalMessages.loadingMsg} </Alert>}
        {success && <Alert color="success">{success} </Alert>}

        <Form onSubmit={handleSubmit(onFormSubmit)}>
          <FormGroup >
            <Label>Nom</Label>
            {errors.username && <Alert color="danger">{errors.username.message}</Alert>}
            <Input
              type="text"
              name="username"
              placeholder="Alain Soral"
              defaultValue={defaultValues.username || ''}
              onChange={(e) => setValue('username', e.target.value)}
            />
          </FormGroup>


          <FormGroup >
            <Label>Email</Label>
            {errors.email && <Alert color="danger">{errors.email.message}</Alert>}
            <Input
              type="text"
              name="email"
              autoCapitalize="none"
              placeholder="alain.soral@er.fr"
              defaultValue={defaultValues.email || ''}
              onChange={(e) => setValue('email', e.target.value)}
            />
          </FormGroup>


          <FormGroup >
            <Label>Mot de passe</Label>
            {errors.password &&  <Alert color="danger">{errors.password.message}</Alert>}
            <Input
              type="text"
              name="password"
              autoCapitalize="none"
              placeholder="quenelle"
              defaultValue={defaultValues.password || ''}
              onChange={(e) => setValue('password', e.target.value)}
            />
          </FormGroup>


          <FormGroup >
            <Label>Adresse</Label>
              {errors.adress && <Alert color="danger">{errors.adress.message}</Alert>}
            <Input
              type="text"
              name="adress"
              autoCapitalize="none"
              placeholder="10 rue la monnaie"
              defaultValue={defaultValues.adress || ''}
              onChange={(e) => setValue('adress', e.target.value)}
            />
          </FormGroup>


          <FormGroup >
            <Label>Ville</Label>
            {errors.city &&<Alert color="danger">{errors.city.message}</Alert>}
            <Input
              type="text"
              name="city"
              autoCapitalize="none"
              placeholder="Birkenau"
              defaultValue={defaultValues.city || ''}
              onChange={(e) => setValue('city', e.target.value)}
            />
          </FormGroup>


          <FormGroup >
            <Label>Téléphone</Label>
            {errors.phone && <Alert color="danger">{errors.phone.message}</Alert>}
            <Input
              type="tel"
              name="phone"
              autoCapitalize="none"
              placeholder="0836656565"
              defaultValue={defaultValues.phone || ''}
              onChange={(e) => setValue('phone', e.target.value)}
            />
          </FormGroup>



          <Button type="submit" disabled={loading} color="primary">{loading ? 'Loading' : 'S\'inscrire'}</Button>
        </Form>
    </Container>
    </React.Fragment>
  </Template>
  );
};



Registration.propTypes = {
  error: PropTypes.string,
  loading: PropTypes.bool,
  success: PropTypes.string,
  defaultValues: PropTypes.shape({
    email: PropTypes.string,
    username : PropTypes.string,
    password : PropTypes.string,
    adress: PropTypes.string,
    city: PropTypes.string,
    phone : PropTypes.string,
  }),
  onFormSubmit: PropTypes.func,
};

Registration.defaultProps = {
  error: null,
  success: null,
  loading: false,
  defaultValues: {
    email: "quentin.sierocki@gmail.com",
    username : "quentin",
    password : "quentin",
    adress:"16 cité du perier",
    city : "bordeaux",
    phone : "0980505215"
  },
};

export default Registration;
