import moment from 'moment';
import axios from '../lib/api';
import HandleErrorMessage from '../lib/format-error-messages';
import initialState from '../store/products';
import Config from '../constants/config';
import { getFeaturedImageUrl } from '../lib/images';
import { ucfirst, stripHtml } from '../lib/string';
import { errorMessages, successMessages } from '../constants/messages';
import pagination from '../lib/pagination';

export default {
  namespace: 'products',

  /**
   *  Initial state
   */
  state: initialState,

  /**
   * Effects/Actions
   */
  effects: (dispatch) => ({

    /**
     * Get a single item from the API
     * @returns {Promise[obj]}
     */
    async fetchProduct() {
      try {
        /*const response = await Api.get(`/v1/users`);
        const { data } = response;

        if (!data) {
          throw new Error({ message: errorMessages.usersEmpty });
        }

        return data;*/
        return initialState.myProducts;
      } catch (error) {
        throw HandleErrorMessage(error);
      }
    },

    /**
     * Get a single item from the API
     * @returns {Promise[obj]}
     */
    async deleteProduct(idProduct) {
      try {
        const response = await axios.get("/product/cancel/"+idProduct);
        const { data } = response;
        if (!data || data.status != "Indisponible") {
          throw new Error(  errorMessages.errorCancelProduct );
        }
        dispatch.products.deleteReduxProduct(idProduct)
        return successMessages.successCancelProduct;
        //return initialState[0];
      } catch (error) {
        throw HandleErrorMessage(error);
      }
    },

    /**
     * Save date to redux store
     * @param {obj} data
     * @returns {Promise[obj]}
     */
    async save(product) {
      try {
        product.productTypeName = product.name;
        product.initialQuantity = product.quantity;
        product.typeGeneral=product.status;
        product.categories=Object.keys(product.listStatus).filter(key => {if(product.listStatus[key]) return key;});
        const response = await axios.post("/product/save",product);
        const { data } = response;
        if (!data) {
          throw new Error(  errorMessages.usersEmpty );
        }
        dispatch.products.addProductToMyProducts(data)
        return successMessages.defaultForm; // Message for the UI
      } catch (error) {
        throw HandleErrorMessage(error);
      }
    },


  }),

  /**
   * Reducers
   */
  reducers: {

    deleteReduxProduct(state, id) {
      state.myProducts = [...state.myProducts].filter(product => product.id != id)
      return state;
    },

    initReduxProducts(state,products){
      state.myProducts=products;
      return state;
    },

    addProductToMyProducts(state,product){
      state.myProducts= [...state.myProducts].concat(product)
      return state;
    },

    replaceListStatus(state,payload){
      state.listStatus=payload;
      return {...state};
    }
  }
};
